import React from 'react';
import './ProjectsPage.css';

const projects = [
  {name: `IPSUM - Basic simulation of <a href='https://en.wikipedia.org/wiki/The_Button_(Reddit)'> 'The Button'</a> experiment`, programs:"React.js", href:"ipsum", imgSrc:"img/ipsum.png"},
  {name: "MEMORY GAME", programs:"JavaScript, Bootstrap", href: "memory-game/index.html", imgSrc:"img/memo-game.jpg"},
  {name: "TO-DO-LIST", programs:"React.js", href:"to-do-list", imgSrc:"img/to-do-list.jpg"},
  {name: "ROCK, PAPER OR SCISSORS GAME", programs:"React.js", href:"rock-paper-scissors", imgSrc:"img/rock.jpg"},
  {name: "CALCULATOR", programs:"JavaScript", href: "5-calculadora/begin/calculator.html", imgSrc:"img/calculator.jpg"},
  {name: "FEED A STAR MOLE GAME", programs:"JavaScript", href: "game/index.html", imgSrc:"img/moles.jpg"},  
  {name: "DOGGOS", programs:"JavaScript", href: "doggos/index.html", imgSrc:"img/doggos.jpg"},
  {name: "LUNA GALLERY", programs:"JavaScript", href: "gallery-2/index.html", imgSrc:"img/luna-gallery.jpg"},
  {name: "ALIEN ABDUCTION ORDER FORM", programs:"CSS, HTML", href: "4-forms/begin/forms.html", imgSrc:"img/abduction-form.jpg"},
  {name: "ZORGBLAG'S BLOG", programs:"CSS, HTML", href: "blog-begin/blog.html", imgSrc:"img/zorbalas-blog.jpg"},
]

projects.map(project => {
  return <li className="li-projects"><a href={project.href}>{project.name}{project.programs}<img src={project.imgSrc} alt={project.name}/></a></li>
})


export class ProjectsPage extends React.Component{
  render(){
    return (
      <div>
        <h1 className="h1-projects">Projects</h1>
          <section className="project-list">
            <ul className="ul-projects">
            {
                projects.map(project => {
                  return (
                  <li className="li-projects">
                    <div dangerouslySetInnerHTML={{__html: project.name}} className="project-titles"></div> 
                    <div className="project-programs">{project.programs}</div>
                    <div className="outlay">
                      <a href={project.href} className="overlay">
                      </a>
                      <a href={project.href} className="overlay-btn">
                      Go to project <span className="arrow-right">→</span>
                      </a>
                      <a href={project.href}>
                      <img src={project.imgSrc} alt={project.name}/>
                      </a>
                    </div>
                  </li>
                  );
                })
            }
            </ul>
          </section>
      </div>
    )
  }
}